const Button = ({ total, setTotal }) => {
  // comportement
  const handleClick = (e) => {
    setTotal(total.concat(e.target.name));
  };

  const handleClear = () => {
    setTotal("");
  };

  const handleDelete = () => {
    setTotal(total.slice(0, -1));
  };

  const handleResult = () => {
    try {
      setTotal(eval(total).toString());
    } catch {
      setTotal("Error");
    }
  };

  // state
  return (
    <div>
      <div className="keypad">
        <button onClick={handleClear} className="highlight" id="clear">
          AC
        </button>
        <button onClick={handleDelete} className="highlight">
          DEL
        </button>
        <button name="/" onClick={handleClick} className="highlight">
          &divide;
        </button>
        <button name="7" onClick={handleClick}>
          7
        </button>
        <button name="8" onClick={handleClick}>
          8
        </button>
        <button name="9" onClick={handleClick}>
          9
        </button>
        <button name="*" onClick={handleClick} className="highlight">
          &times;
        </button>
        <button name="4" onClick={handleClick}>
          4
        </button>
        <button name="5" onClick={handleClick}>
          5
        </button>
        <button name="6" onClick={handleClick}>
          6
        </button>
        <button name="-" onClick={handleClick} className="highlight">
          -
        </button>
        <button name="1" onClick={handleClick}>
          1
        </button>
        <button name="2" onClick={handleClick}>
          2
        </button>
        <button name="3" onClick={handleClick}>
          3
        </button>
        <button name="+" onClick={handleClick} className="highlight">
          +
        </button>
        <button name="0" onClick={handleClick}>
          0
        </button>
        <button name="." onClick={handleClick}>
          .
        </button>
        <button onClick={handleResult} className="highlight" id="equal">
          =
        </button>
      </div>
    </div>
  );
};

export default Button;
